import React from "react";

export default function Info(props) {
  return (
    <div className="row col-md-12">
      <div className="col-md-1 profile-icon">
        <i className={"fa " + props.icon}></i>
      </div>
      <div className="col-md-11 info-text">
        <a href={props.href} className={props.clickable}>
          {props.text}
        </a>
      </div>
    </div>
  );
}
